<script>
	const App = getApp();
	export default {
		data() {
			return {
				baseUrl: '',
				bannerTop: 0,
				bannerTop1: 0,
				navHeight: 0,
				marginBottom: 82,
				filter: false,
				blur: 0,
				scale: 1,
				opacity: 1,
			};
		},
		onReady() {
			//状态栏高度
			this.iStatusBarHeight = uni.getSystemInfoSync().statusBarHeight;
			//胶囊高度
			// 获取微信胶囊的位置信息 width,height,top,right,left,bottom
			this.customHeight = wx.getMenuButtonBoundingClientRect()
			// 导航栏高度(标题栏高度) = 胶囊高度 + (顶部距离 - 状态栏高度) * 2
			this.navigationBarHeight = this.customHeight.height + (this.customHeight.top - this.iStatusBarHeight) * 2
			// 总体高度 = 状态栏高度 + 导航栏高度
			this.navHeight = this.navigationBarHeight + this.iStatusBarHeight

			this.bannerTop = this.navHeight;
			this.bannerTop1 = this.navHeight + 20;
		},
		onShow() {
			this.setTabBarIndex(0);
			
			this.setTabBarRed();
			


		},
		onPageScroll: function(e) {
			uni.$emit('onPageScroll', e.scrollTop)


			const maxScrollDistance = 100;
			const blurIncrement = 20 / maxScrollDistance;
			const scaleIncrement = 1.2 / 50;

			const angle = this.clamp(e.scrollTop, 0, maxScrollDistance);
			this.blur = this.calculateIncrement(angle, blurIncrement, 0, 20);
			this.scale = this.calculateIncrement(angle, scaleIncrement, 1, 1.2);
		},
		methods: {
			getScrollTop() {
				return window.scrollY || document.documentElement.scrollTop;
			},
			clamp(value, min, max) {
				return Math.min(Math.max(value, min), max);
			},
			calculateIncrement(value, increment, defult, aaa) {
				return this.clamp(value * increment, defult, aaa);
			},
		}

	}
</script>

